import './index.css';
import './images/sprites/ironman/firstType.png';
import './images/sprites/ironman/secondType.png';
import './images/sprites/spiderman/firstType.png';
import './images/sprites/spiderman/secondType.png';
import './images/sprites/blackpanther/firstType.png';
import './images/sprites/blackpanther/secondType.png';
import './images/sprites/hulk/firstType.png';
import './images/sprites/hulk/secondType.png';

const spritePath = './src/components/hero/images/sprites';
const takeHitSound = document.querySelector('.hero-take-hit-sound');
const makeHitSound = document.querySelector('.hero-make-hit-sound');
const gameOverSound = document.querySelector('.game-over-sound');

const maxPh = 100;

class Hero {
  constructor([name, type]) {
    this.battleUserHero = document.querySelector('.battle-user-hero');
    this.name = name;
    this.type = type;
    this.Ph = maxPh;
    this.sprites = {
      firstType: `${spritePath}/${type.toLowerCase()}/firstType.png`,
      secondType: `${spritePath}/${type.toLowerCase()}/secondType.png`,
    };
    this.hitType = this.sprites.firstType;
    this.hitTypeString = 'firstType';
    this.battleUserHero.style.background = `url(${this.hitType})`;
  }

  getName() {
    return this.name;
  }

  getType() {
    return this.type;
  }

  getPh() {
    return this.Ph;
  }

  setPh(Ph) {
    this.Ph = Ph;
  }

  getHitType() {
    return this.hitType;
  }

  setHitType(hitType) {
    this.hitType = `${this.sprites[hitType]}`;
    this.hitTypeString = hitType;
    this.battleUserHero.style.background = `url(${this.hitType})`;
  }

  hit() {
    const animationTime = 1400;

    makeHitSound.play();
    this.battleUserHero.classList.add(`battle-${this.type.toLowerCase()}-${this.hitTypeString}-hit`);
    return new Promise((resolve) => {
      setTimeout(() => {
        this.battleUserHero.classList.remove(`battle-${this.type.toLowerCase()}-${this.hitTypeString}-hit`);
        resolve();
      }, animationTime);
    });
  }

  takeHit() {
    const animationTime = 600;

    takeHitSound.play();
    this.battleUserHero.classList.add('battle-hero-shaked');
    return new Promise((resolve) => {
      setTimeout(() => {
        this.battleUserHero.classList.remove('battle-hero-shaked');
        resolve();
      }, animationTime);
    });
  }

  userKill() {
    const animationTime = 1400;

    gameOverSound.play();
    this.battleUserHero.classList.add('battle-hero-killed');
    return new Promise((resolve) => {
      setTimeout(() => {
        resolve();
      }, animationTime);
    });
  }
}

export default Hero;
